body{
    background-color: rgb(71, 6, 71);
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif ;
}
h1,h2,h3,h4,h5,h6{
    text-transform: capitalize;
}
main{
    background-color: #fff;
    width: 70%;
    height:auto;
    margin: 100px auto;
    padding: 30px;
    box-sizing: border-box;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas: 'article section section';
}
article{
    grid-area: article;
    width: 100%;
    margin: 0px auto;
}
section{
    grid-area: section;
}
.container{
    width: 100%;
    height: auto;
    margin: 5px auto;
    padding: 1px 10px;
    box-sizing: border-box;
    background-color: white;
    box-shadow: 0px 1px 5px 0.4px rgb(175, 173, 173);
}
.question-container{
    display: flex;
    justify-content: space-between;
}
.questions > p:nth-child(odd){
    font-weight: bold;
    color: #333;
}
.btn-container{
    align-self:center;
}
button{
    width: 100%;
    border-radius: 50%;
    font-weight: bold;
    border: 0;
    padding: 5px 5px 2px 5px;
    background-color: rgb(228, 222, 222);
}
button:hover{
    background-color: rgb(172, 166, 166);
}
button:active{
    background-color: rgb(228, 222, 222);
}
.btn-icon{
    color: red;
    font-weight: bold;
}